@import url(https://fonts.googleapis.com/css?family=Trade%20Winds&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(//fonts.googleapis.com/css?family=Arimo);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
    background-color: #333;
    color: #d3d3d3;
    font-size: 18px;
}

body {
    font-family: 'Roboto', sans-serif;
}

h1 {
    font-family: 'Trade Winds', serif;
}

.App {
    text-align: center;
    max-width: 700px;
    margin: auto;
    padding: 1rem;
}

.introduction {
    --delay-base: 2000ms;
}

.introduction .small-print {
    font-size: 0.66rem;
}

.introduction .small-print em {
    font-weight: bold;
}

.introduction p:nth-child(2) {
    -webkit-animation-delay: var(--delay-base);
            animation-delay: var(--delay-base);
}

.introduction p:nth-child(3) {
    -webkit-animation-delay: calc(2 * var(--delay-base));
            animation-delay: calc(2 * var(--delay-base));
}

.introduction p:nth-child(4) {
    -webkit-animation-delay: calc(3 * var(--delay-base));
            animation-delay: calc(3 * var(--delay-base));
}

.introduction p:nth-child(5) {
    -webkit-animation-delay: calc(4 * var(--delay-base));
            animation-delay: calc(4 * var(--delay-base));
}

.introduction h2 {
    font-family: 'Montserrat', sans-serif;
    font-size: 4rem;
    -webkit-animation-delay: calc(6 * var(--delay-base));
            animation-delay: calc(6 * var(--delay-base));
}

.introduction p:nth-child(7) {
    -webkit-animation-delay: calc(7 * var(--delay-base));
            animation-delay: calc(7 * var(--delay-base));
}

.introduction button {
    -webkit-animation-delay: calc(8 * var(--delay-base));
            animation-delay: calc(8 * var(--delay-base));
}

.introduction p:nth-child(9) {
    -webkit-animation-delay: calc(8 * var(--delay-base));
            animation-delay: calc(8 * var(--delay-base));
}

button {
    padding: 0.5rem;
    border-radius: 5px;
    font-size: 1rem;
    font-family: 'Trade Winds', serif;
    cursor: pointer;
}

.death-count .odometer {
    display: inline-block;
    font-size: 3rem;
}

.death-count .buttons {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    vertical-align: middle;
    margin-bottom: 1rem;
    margin-top: 1rem;
}

.death-count .buttons button {
    width: 5rem;
}

.death-count > div:last-child {
    margin-top: 1rem;
}

.affected-countries {
    margin-bottom: 1rem;
}

.affected-countries__country-checkboxes {
    display: flex;
    flex-wrap: wrap;
    margin: 2rem auto;
    max-width: 21rem;
    font-size: 0.9rem;
}

.affected-countries__country-checkboxes .countryCheckbox {
    flex-basis: 50%;
    text-align: left;
}

.affected-countries__country-checkboxes .countryCheckbox .rc-checkbox {
    margin-right: 0.5rem;
    margin-left: 0.5rem;
}

.affected-countries__country-checkboxes .countryCheckbox .flag-icon {
    margin-right: 0.5rem;
}

.affected-countries__risk-count {
    flex: 1.5 1;
}

.affected-countries__risk-count .odometer {
    font-size: 1.8rem;
}

.panic-stats div[type=pie] {
    display: inline-block;
}

.panic-stats button {
    margin-top: 1rem;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}

.as-deadly-as {
    --animate-delay: 2s;
}

.as-deadly-as table {
    margin: auto;
    border-collapse: collapse;
}

.as-deadly-as tr {
    height: 3rem;
    color: black;
}

.as-deadly-as th {
    padding: 1rem 1rem;
}

.as-deadly-as tr td {
    padding: 0 1rem;
}

.as-deadly-as tr:nth-child(1) {
    -webkit-animation-delay: calc(var(--animate-delay));
            animation-delay: calc(var(--animate-delay));
    background-color: #FC6042;
}

.as-deadly-as tr:nth-child(2) {
    -webkit-animation-delay: calc(2 * var(--animate-delay));
            animation-delay: calc(2 * var(--animate-delay));
    background-color: #FCB941;
}

.as-deadly-as tr:nth-child(3) {
    -webkit-animation-delay: calc(3 * var(--animate-delay));
            animation-delay: calc(3 * var(--animate-delay));
    background-color: #EEE657;
}

.as-deadly-as tr:nth-child(4) {
    -webkit-animation-delay: calc(4 * var(--animate-delay));
            animation-delay: calc(4 * var(--animate-delay));
    background-color: #2CC990;
}

.as-deadly-as tr:nth-child(5) {
    -webkit-animation-delay: calc(5 * var(--animate-delay));
            animation-delay: calc(5 * var(--animate-delay));
    background-color: #2C82C9;
}

.as-deadly-as tr:nth-child(6) {
    -webkit-animation-delay: calc(6 * var(--animate-delay));
            animation-delay: calc(6 * var(--animate-delay));
    background-color: lightgrey;
}

.as-deadly-as > div:last-child {
    margin-top: 2rem;
    -webkit-animation-delay: calc(8 * var(--animate-delay));
            animation-delay: calc(8 * var(--animate-delay));
}

.final-summary ul {
    text-align: left;
    display: inline-block;
    margin: 2rem auto;
}
.odometer.odometer-auto-theme, .odometer.odometer-theme-car {
    display: inline-block;
    vertical-align: middle;
    *vertical-align: auto;
    *zoom: 1;
    *display: inline;
    position: relative;
}

.odometer.odometer-auto-theme .odometer-digit, .odometer.odometer-theme-car .odometer-digit {
    display: inline-block;
    vertical-align: middle;
    *vertical-align: auto;
    *zoom: 1;
    *display: inline;
    position: relative;
}

.odometer.odometer-auto-theme .odometer-digit .odometer-digit-spacer, .odometer.odometer-theme-car .odometer-digit .odometer-digit-spacer {
    display: inline-block;
    vertical-align: middle;
    *vertical-align: auto;
    *zoom: 1;
    *display: inline;
    visibility: hidden;
}

.odometer.odometer-auto-theme .odometer-digit .odometer-digit-inner, .odometer.odometer-theme-car .odometer-digit .odometer-digit-inner {
    text-align: left;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
}

.odometer.odometer-auto-theme .odometer-digit .odometer-ribbon, .odometer.odometer-theme-car .odometer-digit .odometer-ribbon {
    display: block;
}

.odometer.odometer-auto-theme .odometer-digit .odometer-ribbon-inner, .odometer.odometer-theme-car .odometer-digit .odometer-ribbon-inner {
    display: block;
    -webkit-backface-visibility: hidden;
}

.odometer.odometer-auto-theme .odometer-digit .odometer-value, .odometer.odometer-theme-car .odometer-digit .odometer-value {
    display: block;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
}

.odometer.odometer-auto-theme .odometer-digit .odometer-value.odometer-last-value, .odometer.odometer-theme-car .odometer-digit .odometer-value.odometer-last-value {
    position: absolute;
}

.odometer.odometer-auto-theme.odometer-animating-up .odometer-ribbon-inner, .odometer.odometer-theme-car.odometer-animating-up .odometer-ribbon-inner {
    -webkit-transition: -webkit-transform 2s;
    transition: -webkit-transform 2s;
    transition: transform 2s;
    transition: transform 2s, -webkit-transform 2s;
}

.odometer.odometer-auto-theme.odometer-animating-up.odometer-animating .odometer-ribbon-inner, .odometer.odometer-theme-car.odometer-animating-up.odometer-animating .odometer-ribbon-inner {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}

.odometer.odometer-auto-theme.odometer-animating-down .odometer-ribbon-inner, .odometer.odometer-theme-car.odometer-animating-down .odometer-ribbon-inner {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}

.odometer.odometer-auto-theme.odometer-animating-down.odometer-animating .odometer-ribbon-inner, .odometer.odometer-theme-car.odometer-animating-down.odometer-animating .odometer-ribbon-inner {
    -webkit-transition: -webkit-transform 2s;
    transition: -webkit-transform 2s;
    transition: transform 2s;
    transition: transform 2s, -webkit-transform 2s;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.odometer.odometer-auto-theme, .odometer.odometer-theme-car {
    border-radius: 0.34em;
    font-family: "Arimo", monospace;
    padding: 0.15em;
    background: #000;
    color: #eee0d3;
}

.odometer.odometer-auto-theme .odometer-digit, .odometer.odometer-theme-car .odometer-digit {
    box-shadow: inset 0 0 0.3em rgba(0, 0, 0, 0.8);
    background-size: 100%;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#333333), color-stop(40%, #333333), color-stop(60%, #101010), color-stop(80%, #333333), to(#333333));
    background-image: linear-gradient(to bottom, #333333 0%, #333333 40%, #101010 60%, #333333 80%, #333333 100%);
    padding: 0 0.15em;
}

.odometer.odometer-auto-theme .odometer-digit:first-child, .odometer.odometer-theme-car .odometer-digit:first-child {
    border-radius: 0.2em 0 0 0.2em;
}

.odometer.odometer-auto-theme .odometer-digit .odometer-digit-inner, .odometer.odometer-theme-car .odometer-digit .odometer-digit-inner {
    left: 0.15em;
}

.odometer.odometer-auto-theme.odometer-animating-up .odometer-ribbon-inner, .odometer.odometer-auto-theme.odometer-animating-down.odometer-animating .odometer-ribbon-inner, .odometer.odometer-theme-car.odometer-animating-up .odometer-ribbon-inner, .odometer.odometer-theme-car.odometer-animating-down.odometer-animating .odometer-ribbon-inner {
    -webkit-transition-timing-function: linear;
            transition-timing-function: linear;
}
